/* curousel 2 */
.slider2.slick-slider .slick-prev {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  transform: translate(150%, -50%);
  cursor: pointer;
  color: #0062cc;
  border: none;
  outline: none;
  z-index: 1;
}
.slider2.slick-slider .slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 30px;
  height: 20px;
  padding: 0;
  transform: translate(-150%, -50%);
  cursor: pointer;
  color: #0062cc;
  border: none;
  z-index: 1;
  outline: none;
}

.slider2.slick-slider .slick-prev:hover,
.slider2.slick-slider .slick-prev:focus,
.slider2.slick-slider .slick-next:hover,
.slider2.slick-slider .slick-next:focus {
  color: #0062cc;
