/* ----------------------------------- */
/* responsive media */
/* - mobile devices(all) */
@media (min-width: 100px) and (max-width: 766px) {
  header {
    transform: translateY(-26%) !important;
    position: fixed;
    transition: 1s;
    border-bottom: 1px solid #ccc;
    min-width: 100%;
    background-color: #fff;
    z-index: 9;
    padding-bottom: 0px;
  }
  header:hover {
    transform: none;
  }
  .menu-inner {
    max-width: 1300px;
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;
    display: block;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    align-items: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
  }
  .footer-div-text {
    position: absolute;
    transform: translate(13%, 0%);
    width: 80%;
    height: 100px;
    background-color: #000;
    align-items: center;
    font-size: 11px;
    display: flex;
    color: #fed501;
    justify-content: space-around;
    border-radius: 10px;
    font-weight: 800;
  }
  .footer-div-buttom {
    background-color: #fed501;
    color: #000;
    font-size: 9px;
    font-weight: 600;
    padding: 5px 23px;
    border-radius: 5px;
  }
  .search-box-form .login-inp {
    width: 100%;
    height: 36px;
    border-radius: 25px;
    background: transparent;
    border: 1px solid var(--primary-color);
    padding-left: 40px;
    padding-bottom: 0px;
  }
  .search-div {
    margin-bottom: 10px;
  }
  .icon-top-ul > li {
    border-right: 1px solid #ccc;
    padding: 0px 4px;
  }
  .second-menu-ul > li > a {
    display: flex;
    line-height: 13px;
    color: #fff;
    text-decoration: none;
    /* font-family: sans-serif; */
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    font-size: 8px;
    padding: 6px 0px;
  }
  .second-menu-ul > li > a:only-child {
    padding: 5px 15px;
  }
  .Client-testinomial-buttom {
    position: relative;
    width: auto;
    background: #ffc107;
    background-size: 200% auto;
    color: var(--bg-white);
    font-family: var(--title-fonts);
    font-size: 15px;
    font-weight: 700;
    padding: 7px 26px;
    line-height: 2;
    vertical-align: middle;
    border: none;
    display: inline-block;
    overflow: hidden;
    border-radius: 5px;
    margin-top: 28px;
    margin-bottom: 25px;
  }
  .client-test-div {
    height: 0vh;
  }
  .section-margin {
    padding-top: 0px;
    padding-bottom: 0px;
  }
  .btn-link {
    font-size: 13px;
    color: var(--bg-black);
    text-decoration: none;
    font-weight: 600;
  }
  .btn-link:hover {
    color: var(--bg-black);
    text-decoration: none;
    border: none;
    font-size: 13px;
    font-weight: 600;
    border: 1px solid transparent;
  }
  .slider-heading {
    text-align: center;
    color: #436fe8;
    font-size: 25px;
    font-weight: 700;
    position: relative;
    margin-bottom: 50px;
    margin-top: 50px;
  }
  .slider-heading::after {
    content: "";
    position: absolute;
    width: 68px;
    text-align: center;
    top: 134%;
    right: 40%;
    border-radius: 20px;
    height: 4px;
    /* transform: translate(-146px, 20px); */
    background-color: #fed501;
  }
  .elementor-icon-list-img {
    width: 100%;
    margin-top: 10%;
  }
  .pt-button {
    position: relative;
    width: auto;
    background: var(--primary-color);
    background-size: 200% auto;
    color: var(--bg-white);
    font-family: var(--title-fonts);
    font-size: 16px;
    padding: 7px 26px;
    line-height: 2;
    vertical-align: middle;
    border: none;
    display: inline-block;
    overflow: hidden;
    border-radius: 5px;
    margin-top: 19px;
  }
  .pt-footer-social ul {
    margin: 0;
    padding: 0;
    float: left;
  }
  .pt-footer-social ul li a {
    background: var(--bg-white);
    color: var(--primary-color);
    width: 30px;
    height: 31px;
    line-height: 53px;
    text-align: center;
    display: inline-block;
    border-radius: 5px;
  }
  footer#pt-footer .pt-copyright-footer .pt-copyright a {
    color: #fff;
    font-size: 14px;
    font-weight: 500;
  }
  .grank-title {
    line-height: 20px;
    text-align: left;
    font-size: 12px;
    margin-bottom: 38px;
  }
  #pt-footer .pt-footer-style-1 .pt-footer-logo {
    height: 68px;
    margin-bottom: 13px;
    margin-top: 36px;
    width: 193px;
    border-radius: 5px;
  }
  .text-align-div {
    text-align: left;
  }
  .footer-white-bg-padding {
    padding: 0px 20px;
  }
  footer#pt-footer .widget .footer-title {
    margin-top: 23px;
    padding: 0;
    font-size: 21px;
    position: relative;
    margin-bottom: 0px;
    line-height: 43px;
  }
  footer#pt-footer .pt-footer-style-1 .widget ul.menu li a,
  footer#pt-footer .pt-footer-style-1 .widget ul li a {
    color: var(--bg-white);
    line-height: 16px;
    font-size: 14px;
  }
  .pt-contact {
    font-size: 14px;
    line-height: 21px;
  }
  footer#pt-footer .pt-copyright-footer .pt-copyright {
    text-align: left;
    /* padding: 12px 0px; */
    padding: 5px 0px;
    display: inline-block;
    width: 100%;
  }
  #order1 {
    order: 2;
  }
  #order2 {
    order: 1;
  }
  .nav-link2.active {
    background-size: 107% 100%;
    border: none;
    /* padding: 0px 80px; */
    background-repeat: no-repeat;
    font-size: 9px;
    font-weight: bold;
    height: 48px;
    /* max-width: 300px; */
    background-color: #436fe8;
    width: 100%;
    color: #fed501;
  }
  .nav-link2 {
    /* background-image: url(../../public/assest/photos/blue-back-arrow2.png); */
    background-size: 100% 100%;
    border: none;
    background-repeat: no-repeat;
    font-size: 9px;
    height: 47px;
    font-weight: bold;
    background-color: #436fe8;
    color: #fff;
    width: 100%;
    text-align: center;
  }
  .nav-item2 {
    /* display: block; */
    width: 72px;
    /* margin: 0 auto; */
    text-align: center;
  }
  .banner-new-div > h3 {
    font-size: 9px;
    line-height: 28px;
    margin: 0px 1px;
  }
  .banner-new-div > h3 > a {
    background-color: #000;
    padding: 4px 18px;
    border-radius: 4px;
  }
  .banner-new-div > h2 {
    font-size: 10px;
    line-height: 13px;
    margin: 4px 0px;
  }
  .banner-new-div > h1 {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 17px;
    font-family: serif;
    max-width: 139px;
    min-height: 40px;
  }
  .slider-heading-top {
    position: absolute;
    text-align: center;
    left: 36%;
    bottom: 8%;
    color: #436fe8;
    font-size: 19px;
    font-weight: 700;
  }
  .slider-heading-top::after {
    content: "";
    position: absolute;
    width: 53px;
    text-align: center;
    top: 122%;
    left: 27%;
    border-radius: 20px;
    height: 3px;
    background-color: #fed501;
  }
  .slider-div > .slider-img-absolute1 {
    width: 100%;
    padding: 0px;
    height: 100%;
    /* border: 2px solid rgba(0, 123, 255, 0.25);
    box-shadow: 0px 0px 16px 0px rgba(0, 123, 255, 0.25);
    border-radius: 25px; */
  }
  .selecter-div {
    border: 1px solid #436fe8;
    border-radius: 18px;
    color: #436fe8;
    width: 100%;
    margin: 8px 0px;
    padding: 3px 12px;
  }
  .selecter-section > h1 {
    font-size: 16px;
    font-weight: 700;
    color: #436fe8;
  }
  .selecter-section {
    display: block;
  }
  .banner-new-div {
    position: absolute;
    top: 5%;
    text-align: end;
    right: 2%;
  }
  .elementor-icon-list-item {
    margin-bottom: 25px;
    margin-left: 29px;
  }
  .nav-tab-text {
    align-self: center;
    padding: 0px 0px;
  }
  .pt-button3 {
    margin: 20px 20px;
  }
  .elementor-icon-list-img2 {
    width: 101%;
    float: left;
  }

  .testinomial-section-img {
    width: 73%;
    height: auto;
    margin-left: 42px;
    text-align: center;
    animation: shakeX 1s;
  }
  .testinomial-section-name {
    transform: translate(-7%, -53%);
  }
  #order1 {
    order: 1;
  }
  #order2 {
    order: 2;
  }
  .second-menu {
    background-color: #436fe8;
    padding: 0px 0px;
  }
  .dno {
    padding: 4px 0px;
  }
  .icon-top-ul > li > a > img {
    height: 22px;
  }
}

